import React from 'react';

const ObservingDiv: React.FC<any> = ({children, className, ...rest}) => {
  const ref = React.useRef<HTMLDivElement>(null);
  React.useEffect(()=>{
    // @ts-ignore
    const observer : IntersectionObserver = window.intersectionObserver;
    const current =  ref.current;
    current && observer.observe(current);
    setTimeout(()=> {
      if (current) {
        const top = current.offsetTop;
        const scrollTop = document.scrollingElement?.scrollTop || 0;
        if (scrollTop + window.innerHeight - top > 100 ) {
          current.classList.toggle("observed", true);
        }
      }
    }, 100)
    return () => {
      current && observer.unobserve(current);
    }
  })
  return <div className={`observing ${className || ""}`} {...rest} ref={ref}>
    {children}
  </div>
}

export default ObservingDiv;